import {
	applyStyle,
	applyTemplateDefaults,
	getValue,
	renderTemplate,
} from '../../utils';

import {actionTypes, createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import stylesheet from './styles.scss';
import packageJson from '../../../package.json';

const {
	COMPONENT_DOM_READY,
	COMPONENT_DOM_TREE_READY,
	COMPONENT_TREE_RENDERED,
} = actionTypes;

export const NOW_GRID_DOM_READY = 'NOW_GRID#DOM_READY';
export const NOW_GRID_DOM_TREE_READY = 'NOW_GRID#DOM_TREE_READY';
export const NOW_GRID_TREE_RENDERED = 'NOW_GRID#TREE_RENDERED';

// These are the default templates functions that comprise the table.
// Users can pass in override to any or all of these.  Any functions
// not overridden will remain at their defaults.
//
// This allows users to override just the parts of the table they
// want, and keep the existing rendering for the rest with little
// overhead or effort.
export const templateDefaults = {
	table: (context) => {
		const {templates, properties} = context;
		const {columns, rowData, tableLayout} = properties;
		const tableContext = {...context, columns, rowData};
		const tableClasses = tableLayout;
		const result = [];

		if (templates.tableStyle) {
			result.push(renderTemplate('tableStyle', templates, tableContext));
		}

		result.push(
			<table
				className={tableClasses}
				{...applyStyle(properties.style, context)}>
				{renderTemplate('colGroup', templates, tableContext)}
				{renderTemplate('head', templates, tableContext)}
				{renderTemplate('body', templates, tableContext)}
			</table>
		);

		return result;
	},

	// Just here so you know it's there
	tableStyle: () => undefined,

	colGroup: (context) => {
		const {columns, properties} = context;

		return (
			<colgroup {...applyStyle(properties.style, context)}>
				{columns.map((column) => (
					<col style={{width: getValue(column.width)}} />
				))}
			</colgroup>
		);
	},

	head: (context) => {
		const {templates, properties} = context;
		return (
			<thead {...applyStyle(properties.style, context)}>
				{renderTemplate('headRow', templates, context)}
			</thead>
		);
	},

	headRow: (context) => {
		const {templates, columns, properties} = context;
		return (
			<tr {...applyStyle(properties.style, context)}>
				{columns.map((column, columnIndex) => {
					return renderTemplate('headCell', templates, {
						...context,
						column,
						columnIndex,
					});
				})}
			</tr>
		);
	},

	headCell: (context) => {
		const {column, properties} = context;
		return (
			<th
				className="heading"
				width={getValue(column.width, context)}
				align="left"
				{...applyStyle(properties.style, context)}>
				{getValue(column.title, context)}
			</th>
		);
	},

	body: (context) => {
		const {templates, rowData, properties} = context;

		return (
			<tbody {...applyStyle(properties.style, context)}>
				{rowData.map((row, rowIndex) =>
					renderTemplate('bodyRow', templates, {
						...context,
						row,
						rowIndex,
					})
				)}
			</tbody>
		);
	},

	bodyRow: (context) => {
		const {templates, columns, properties} = context;
		return (
			<tr {...applyStyle(properties.style, context)}>
				{columns.map((column, columnIndex) =>
					renderTemplate('bodyCell', templates, {
						...context,
						column,
						columnIndex,
					})
				)}
			</tr>
		);
	},

	bodyCell: (context) => {
		const {row, column, properties} = context;
		const value = column.accessor(row, column, context);
		const displayValue = column.format(value, context);

		return (
			<td
				width={getValue(column.width, context)}
				{...applyStyle(properties.style, context)}>
				{column.render({...context, value, displayValue})}
			</td>
		);
	},
};

const NowGrid = (state, {dispatch, updateState}) => {
	const {properties} = state;
	const {columns, rowData} = properties;
	// Override any templates properties from the caller, leaving defaults in place otherwise
	const templates = applyTemplateDefaults(
		properties.templates,
		templateDefaults
	);
	const context = {
		state,
		properties,
		columns,
		rowData,
		templates,
		dispatch,
		updateState,
	};

	return (
		<div className="container now-grid">
			{renderTemplate('table', templates, context)}
		</div>
	);
};

createCustomElement('now-grid', {
	properties: {
		rowData: {default: []},
		columns: {default: []},
		fixedHead: {default: false},
		tableLayout: {default: undefined},
		templates: {default: {}},
	},
	renderer: {type: snabbdom},
	view: NowGrid,
	styles: stylesheet,
	actionHandlers: {
		[COMPONENT_DOM_READY]: {
			effect: ({dispatch}) => {
				dispatch(NOW_GRID_DOM_READY);
			},
		},
		[COMPONENT_DOM_TREE_READY]: {
			effect: ({dispatch}) => {
				dispatch(NOW_GRID_DOM_TREE_READY);
			},
		},
		[COMPONENT_TREE_RENDERED]: {
			effect: ({dispatch}) => {
				dispatch(NOW_GRID_TREE_RENDERED);
			},
		},
	},
	onConnect(host) {
		if (!window.listComponentRegistry) {
			window.listComponentRegistry = new WeakMap();
		}
		window.listComponentRegistry.set(host, packageJson.version);
	},
	onDisconnect(host) {
		if (window.listComponentRegistry) {
			window.listComponentRegistry.delete(host);
		}
	},
});

export default 'now-grid';
