import _ from 'lodash';
import {createSelector} from 'reselect';
import {makePluginActions} from '../utils';
import u from 'updeep';

const PLUGIN_NAME = 'sortByColumns';

// Exported Selectors
const statePath = ['behaviors', PLUGIN_NAME];
const fieldsPath = [...statePath, 'fields'];
const ordersPath = [...statePath, 'orders'];
const getFieldsDefault = (state) => _.get(state, fieldsPath);
const getOrdersDefault = (state) => _.get(state, ordersPath);
export const selectors = {
	getFields: getFieldsDefault,
	getOrders: getOrdersDefault,
};

// Exported Actions
export const actions = makePluginActions(PLUGIN_NAME, [
	'SET_SORT_ORDER',
	'ADD_SORT_ORDER',
]);

// Exported functions (for unit testing)
export function sortRowData(rowData, fields, orders, columns) {
	// TODO: columns can define a comparator function to use instead of
	//       default numeric and string comparisons.

	return _.orderBy(rowData, fields, orders);
}

// Main export for instantiating the plugin
export function instantiatePlugin(options) {
	// Defaults to writing the sorted data to `state.properties.data`
	const setRowDataDefault = (state, rowData) =>
		u({properties: {rowData}}, state);

	// Imported selectors
	const {
		selectors: {
			getEnabled,
			getRowData,
			getColumns,
			// These can optionally be passed in when maintaining your own sort state by
			// handling the SET_SORT_ORDER and ADD_SORT_ORDER actions at a higher level.
			getFields = getFieldsDefault,
			getOrders = getOrdersDefault,
			setRowData = setRowDataDefault,
		},
	} = options;

	// Derived selectors
	const getSortedData = createSelector(
		getRowData,
		getFields,
		getOrders,
		getColumns,
		(rowData, fields, orders, columns) => {
			console.log('SORTING!!!!!!!');
			return sortRowData(rowData, fields, orders, columns);
		}
	);

	// Export Action Handlers to enable state lifting
	const actionHandlers = {
		// Set new sort order for a single column and direction (normal click on a column header)
		[actions.SET_SORT_ORDER]: ({action, updateState}) => {
			const {field, order} = action.payload;
			const fields = [field];
			const orders = [order];

			updateState({
				path: statePath,
				value: {
					fields,
					orders,
				},
				operation: 'merge',
			});
		},

		// Add a new sort order to the end of the current sort state (when SHIFT CLICK is done)
		[actions.ADD_SORT_ORDER]: ({state, action, updateState}) => {
			const fields = getFields(state);
			const orders = getOrders(state);
			const {field, order} = action.payload;

			// Remove the field if it already exists -- we'll append the new entry to the end in any case
			const fieldIndex = fields.indexOf(field);
			let newFields = fields.slice();
			let newOrders = orders.slice();
			if (fieldIndex >= 0) {
				newFields.splice(fieldIndex, 1);
				newOrders.splice(fieldIndex, 1);
			}

			updateState({
				path: statePath,
				value: {
					fields: [...newFields, field],
					orders: [...newOrders, order],
				},
				operation: 'merge',
			});
		},
	};

	// Return the actual plugin object from this closure
	return {
		transformState(state) {
			if (!getEnabled(state)) {
				return state;
			}

			// Update the state to the specified location
			return setRowData(state, getSortedData(state));
		},

		behavior: {
			name: PLUGIN_NAME,
			initialState: {fields: [], orders: []},
			actionHandlers,
		},

		// Return the action handler map separately as well for easy access.
		// Used when state is lifted and actions are handled at a higher level.
		actionHandlers,
	};
}
