import {getEntryValue, makePluginActions} from '../utils';

import _ from 'lodash';
import {createSelector} from 'reselect';
import u from 'updeep';

const PLUGIN_NAME = 'filterByColumns';

// Exported Selectors
const statePath = ['behaviors', PLUGIN_NAME];
const filtersPath = [...statePath, 'filters'];
const getFiltersDefault = (state) => _.get(state, filtersPath);
export const selectors = {
	getFilters: getFiltersDefault,
};

// Exported Actions
export const actions = makePluginActions(PLUGIN_NAME, ['SET_FILTER_FOR_FIELD']);

// Exported functions (for unit testing)
export function filterData(data, columns, filters) {
	return _.filter(data, (row) => {
		for (const field in filters) {
			const column = _.find(columns, (column) => column.field === field) || {};
			const filterString = _.lowerCase(filters[field]);
			if (filterString !== undefined && filterString.length > 0) {
				// Make sure the value is in a string
				const value = `${getEntryValue(row, column)}`;
				if (value === undefined) {
					return false;
				}

				if (_.lowerCase(value).indexOf(filterString) < 0) {
					return false;
				}
			}
		}
		return true;
	});
}

// Main export for instantiating the plugin
export function instantiatePlugin(options) {
	// Defaults to writing the sorted data to `state.properties.data`
	const setRowDataDefault = (state, rowData) =>
		u({properties: {rowData}}, state);

	// Imported selectors
	const {
		selectors: {
			getEnabled,
			getColumns,
			getRowData,
			getFilters = getFiltersDefault,
			setRowData = setRowDataDefault,
		},
	} = options;

	// Derived selectors
	const getFilteredData = createSelector(
		getFilters,
		getRowData,
		getColumns,
		(filters, data, columns) => {
			console.log('FILTERING!!!!!!!');
			return filterData(data, columns, filters);
		}
	);

	const actionHandlers = {
		// Set new sort order for a single column and direction (normal click on a column header)
		[actions.SET_FILTER_FOR_FIELD]: ({state, action, updateState}) => {
			const {field, filter} = action.payload;
			const filters = getFilters(state);

			// Add or change the filter string
			const newFilters = {...filters, [field]: filter};

			updateState({
				path: filtersPath,
				value: newFilters,
				operation: 'set',
			});
		},
	};

	// Return the actual plugin object from this closure
	return {
		transformState(state) {
			if (!getEnabled(state)) {
				return state;
			}

			// Update the state to the specified location
			return setRowData(state, getFilteredData(state));
		},

		behavior: {
			name: PLUGIN_NAME,
			initialState: {filters: {}},
			actionHandlers,
		},

		actionHandlers,
	};
}
