// Wrapper component example that a Business Unit might create.
// This is usually an opinionated component.

import * as _filterByColumnsPlugin from '../plugins/filterByColumnsPlugin';
import * as _fixedColumnsPlugin from '../plugins/fixedColumnsPlugin';
import * as _keyboardNavigationPlugin from '../plugins/keyboardNavigationPlugin';
import * as _sortByColumnsPlugin from '../plugins/sortByColumnsPlugin';

import {createCustomElement} from '@servicenow/ui-core';
import {createSelector} from 'reselect';
import {headRow as filterByColumnsHeadRow} from '../templates/filterByColumns';
import {table as fixedColumnsTable} from '../templates/fixedColumns';
import {table as fixedHeadTable} from '../templates/fixedHead';
import {bodyCell as keyboardNavigationBodyCell} from '../templates/keyboardNavigation';
import react from '@servicenow/ui-renderer-react';
import {headCell as sortByHeadCell} from '../templates/sortByColumns';
import {style} from '../templates/style';
import stylesheet from './styles.scss';
// TODO: Remove this once the overriding issue is fixed
import {templateDefaults} from '../components/now-grid';

// Simple Selectors
const getRowData = (state) => state.properties.rowData;
const getColumns = () => columns;

const getFixedLeft = (state) => state.properties.fixedLeft;
const getFixedRight = (state) => state.properties.fixedRight;
const getEnableFixedColumns = (state) => state.properties.enableFixedColumns;
const getEnableSorting = (state) => state.properties.enableSorting;
const getEnableFiltering = (state) => state.properties.enableFiltering;
const getFixedHeader = (state) => state.properties.fixedHeader;
const getEnableEditing = (state) => state.properties.enableEditing;
const getEnableKeyboardNavigation = (state) =>
	state.properties.enableKeyboardNavigation;

// TODO: What is the right place in this file for this selector?  Or does it belong elsewhere?
// Make a list of the column names in order - factors in any rearrangements and column hiding
const getColumnOrder = createSelector(
	getFixedLeft,
	getFixedRight,
	getColumns,
	(left, right, columns) => {
		const middle = columns
			.filter(
				(column) =>
					left.indexOf(column.field) === -1 &&
					right.indexOf(column.field) === -1
			)
			.map((entry) => entry.field);

		return [...left, ...middle, ...right];
	}
);

// Instantiate Plugins
const fixedColumnsPlugin = _fixedColumnsPlugin.instantiatePlugin({
	selectors: {
		getFixedLeft,
		getFixedRight,
		getEnabled: getEnableFixedColumns,
		getColumns,
	},
});

const filterByColumnsPlugin = _filterByColumnsPlugin.instantiatePlugin({
	selectors: {getEnabled: getEnableFiltering, getRowData, getColumns},
});

const sortByColumnsPlugin = _sortByColumnsPlugin.instantiatePlugin({
	selectors: {getEnabled: getEnableFiltering, getRowData, getColumns},
});

const keyboardNavigationPlugin = _keyboardNavigationPlugin.instantiatePlugin({
	selectors: {
		getEnabled: getEnableKeyboardNavigation,
		getRowData,
		getColumns,
		getColumnOrder,
		getIsEditingCell: () => false, // getIsEditingCell,
	},
});

// Selectors from instantiated plugins
const {getFixedColumns} = fixedColumnsPlugin.selectors;
const {getFocused} = keyboardNavigationPlugin.selectors;

function BU_Grid(state, {updateState, dispatch, components}) {
	const rowData = getRowData(state);
	const fixedColumns = getFixedColumns(state);

	return (
		<now-grid
			rowData={rowData}
			columns={columns}
			templates={{
				table: {
					render: fixedColumnsTable,
					properties: {...fixedColumns},
					templates: {
						table: {
							render: fixedHeadTable,
							templates: {
								table: templateDefaults.table,
								tableStyle: {
									render: style,
									properties: {
										style: `tr { background: lightblue; }`, // Add !important here to see the style working
									},
								},
							},
							properties: {
								background: 'lightBlue',
								isEnabled: getFixedHeader(state),
							},
						},
					},
				},
				headRow: {
					render: filterByColumnsHeadRow,
					properties: {isEnabled: getEnableFiltering(state)},
					templates: {
						headCell: {
							render: sortByHeadCell,
							properties: {isEnabled: getEnableSorting(state)},
							templates: {headCell: templateDefaults.headCell},
						},
					},
				},
				bodyCell: {
					render: keyboardNavigationBodyCell,
					properties: {focused: getFocused(state)},
				},
			}}
		/>
	);
}

function defineColumn(options) {
	return {
		format: (value) => value,
		render: ({displayValue}) => {
			return displayValue;
		},
		accessor: (row, column, context) => row[options.field],
		...options,
	};
}

export const columns = [
	defineColumn({
		field: 'first',
		title: 'First Name',
		width: '175px', // NOTE: You could choose to use percentages here instead
		type: 'string',
	}),
	defineColumn({
		field: 'last',
		title: 'Last Name',
		width: '175px',
		type: 'string',
	}),
	defineColumn({
		field: 'title',
		title: 'Title',
		width: '200px',
		type: 'string',
	}),
	defineColumn({
		field: 'street',
		title: 'Street',
		width: '200px',
		type: 'string',
	}),
	defineColumn({
		field: 'state',
		title: 'State',
		width: '200px',
		type: 'string',
	}),
	defineColumn({
		field: 'zip',
		title: 'ZIP',
		width: '200px',
		type: 'string',
	}),
	defineColumn({
		field: 'randomNumber',
		title: 'Rando',
		width: '100px',
		type: 'number',
	}),
];

createCustomElement('bu-grid', {
	view: BU_Grid,
	renderer: {type: react},
	styles: stylesheet,
	properties: {
		rowData: {default: []},
		enableFixedColumns: {default: true},
		enableFiltering: {default: false},
		enableSorting: {default: true},
		enableKeyboardNavigation: {default: true},
		fixedHeader: {default: true},
		fixedLeft: {default: []},
		fixedRight: {default: []},
	},
	initialState: {},
	transformState(state) {
		return _.flow(
			fixedColumnsPlugin.transformState,
			filterByColumnsPlugin.transformState,
			sortByColumnsPlugin.transformState
		)(state);
	},
	behaviors: [
		fixedColumnsPlugin.behavior,
		filterByColumnsPlugin.behavior,
		sortByColumnsPlugin.behavior,
		keyboardNavigationPlugin.behavior,
	],
});

function render(state) {
	const {properties} = state;

	return (
		<table>
			<tbody>
				{
					// Iterate over each row
					properties.rows.map((row) => (
						<tr>
							{
								// Iterate over each column
								properties.columns.map((col) => (
									<td>
										{
											// Call the render() function of each column, which can return
											// a custom web component name and a function that returns props.

											col.render({value: row[col.field], row, col})
										}
									</td>
								))
							}
						</tr>
					))
				}
			</tbody>
		</table>
	);
}
