import {renderTemplate} from '../utils';

// Partial render function for a table with a fixed header section and scrollable body
export function table(context) {
	const {templates, properties} = context;
	const tableClasses = 'table-layout-fixed';

	if (properties.isEnabled) {
		const nodes = [];
		if (templates.tableStyle) {
			nodes.push(renderTemplate('tableStyle', templates, context));
		}

		nodes.push(
			<div className="fixed-head">
				<table className={tableClasses}>
					{renderTemplate('colGroup', templates, context)}
					{renderTemplate('head', templates, context)}
				</table>
			</div>
		);

		nodes.push(
			<div className="scroll-container">
				<table className={tableClasses}>
					{renderTemplate('colGroup', templates, context)}
					{renderTemplate('body', templates, context)}
				</table>
			</div>
		);

		return nodes;
	} else {
		return renderTemplate('table', templates, context);
	}
}
