// Template render functions

import {getValue, renderTemplate} from '../utils';

import {actions} from '../plugins/sortByColumnsPlugin';

const {ADD_SORT_ORDER, SET_SORT_ORDER} = actions;

export function headCell(context) {
	const {column, dispatch, properties, templates} = context;
	if (properties.isEnabled) {
		return (
			<th
				className="heading"
				width={getValue(column.width, context)}
				align="left">
				<div style={{display: 'flex', flexDirection: 'row'}}>
					<div style={{flex: 1}}>{getValue(column.title, context)}</div>
					<div>
						<now-button-bare
							icon-start="caret-up-fill"
							size="sm"
							variant="secondary"
							onclick={({shiftKey}) => {
								dispatch(shiftKey ? ADD_SORT_ORDER : SET_SORT_ORDER, {
									field: column.field,
									order: 'asc',
								});
							}}
						/>
						<now-button-bare
							icon-start="caret-down-fill"
							size="sm"
							variant="secondary"
							onclick={({shiftKey}) => {
								dispatch(shiftKey ? ADD_SORT_ORDER : SET_SORT_ORDER, {
									field: column.field,
									order: 'desc',
								});
							}}
						/>
					</div>
				</div>
			</th>
		);
	} else {
		return renderTemplate('headCell', templates, context);
	}
}
