import * as _ from 'lodash';

import moment from 'moment';

export function addAgeInYearsFromDate(rowData, dateFieldName, ageFieldName) {
	return rowData.map((row) => {
		const age = timeSinceDate(row[dateFieldName], 'years'); // TODO: Use accessor()
		return {...row, [ageFieldName]: age};
	});
}

export function timeSinceDate(date, unit = 'days') {
	return moment().diff(date, unit, false);
}

// Build a dynamic style.
//
// fnOrStyle is either a function that will return an object or it's a string `className`.
//
// The result of this function should be applied to a tag with a spread.
//
// If fnOrStyle is a function, context and defaultStyle are passed in and can be used
// or extended as desired.
export function applyStyle(fnOrStyle, context = undefined, defaultStyle = {}) {
	if (!fnOrStyle) {
		return defaultStyle;
	}

	// If it's a function, call it with the context
	if (_.isFunction(fnOrStyle)) {
		fnOrStyle = fnOrStyle(context, defaultStyle);
	}

	// If it's a string, append the class
	// NOTE: Although this "works", since we are using Shadow DOM, only the names of styles defined
	//       by the component where applyStyle() is called will be available.  You cannot pass in the
	//       name of a style from a parent component or a global stylesheet.
	if (_.isString(fnOrStyle)) {
		const newClassName = fnOrStyle + ' ' + _.get(defaultStyle, 'className', '');
		return {className: newClassName, styles: defaultStyle.style};
	}

	// If it's an object, it will be spread onto the object
	if (_.isObject(fnOrStyle)) {
		// TODO: Should we restrict the fields here to `className` and `style`?
		return {...defaultStyle, ...fnOrStyle};
	}

	return defaultStyle;
}

// If the fnOrValue is undefined, return the default value.
//
// If the fnOrValue is a function, call the function with the provided context and return
// that value as the result.
//
// Otherwise just return the fnOrValue as the result.
export function getValue(fnOrValue, context = {}, defaultValue = undefined) {
	// Early out optimization to avoid calling lodash when no value is provided.
	if (!fnOrValue) {
		return defaultValue;
	}

	if (_.isFunction(fnOrValue)) {
		return fnOrValue(context);
	}

	return fnOrValue;
}

// context contains: state, props, data, column, row, columnIndex, rowIndex, styles, dispatch, context
export function getCellContent(context = {}) {
	const {column, row} = context;

	let value = getEntryValue(row, column);

	// Format the value if it has a formatter
	const ret = getValue(column.format || value, {value, ...context});
	return ret;

	// TODO: Fix error when returning straight value.  Snabbdom crashes, so if it is not
	//       renderable, we should wrap is in `${value}`
}

// Use the accessor, if any, to get the data value from the entry.
// Otherwise return the named field of the entry.
export function getEntryValue(row, column) {
	if (column.accessor && _.isFunction(column.accessor)) {
		return column.accessor(row, column);
	}
	console.error(
		`ERROR: Column '${column.field}' is missing an accessor() function!`
	);
	return '<no_accessor>';
}

// Basic aggregation calculation for numeric fields
export function minMaxAvg(data, column) {
	const {min, max, total} = _.reduce(
		data,
		({min, max, total}, row) => {
			const value = getEntryValue(row, column);
			return {
				total: total + value,
				min: Math.min(min, value),
				max: Math.max(max, value),
			};
		},
		{total: 0, min: Number.MAX_VALUE, max: Number.MIN_VALUE}
	);
	return {min, max, total, avg: total / data.length};
}

export function getColumnStyles(column, context) {
	const {data} = context;
	const attrs = ['width', 'textAlign', 'verticalAlign'];

	const style = attrs.reduce((style, attr) => {
		// TODO: column[attr] should be a function that checks for accessor() first and calls it or returns column[attr])
		let attrValue = getValue(column[attr], {
			column,
			data,
		});
		// Special case
		if (attr === 'width' && _.isNumber(attrValue)) {
			attrValue = `${attrValue}px`;
		}
		return attrValue !== undefined ? {...style, [attr]: attrValue} : style;
	}, {});
	return {style};
}

export function capitalizeFirstLetter(string) {
	return string.charAt(0).toUpperCase() + string.slice(1);
}

// Create an object that prefixes the action names with the plugin name
// where the plugin name is converted to uppercase.
// e.g., the SET_FILTER_FOR_FIELD action of the gridFiltering plugin would
//       become GRIDFILTERING#SET_FILTER_FOR_FIELD
export function makePluginActions(pluginName, actionNames) {
	const actionPrefix = _.upperCase(pluginName);
	const actions = {};
	actionNames.forEach((actionName) => {
		actions[actionName] = `${actionPrefix}#${actionName}`;
	});
	return actions;
}

// Merge the current template values with the those for the specified `templateName`.
// This is done in preparation of rendering a template.
export function overrideTemplates(templateName, templates) {
	if (!templates) {
		templates = {};
	}
	return {...templates, ..._.get(templates, [templateName, 'templates'], {})};
}

// Apply the template overrides, then call the template render function.
export function renderTemplate(templateName, templates, context) {
	const nestedTemplates = overrideTemplates(templateName, templates);

	const templateFuncOrObj = templates[templateName];
	if (_.isFunction(templateFuncOrObj)) {
		// Use the function directly
		return templateFuncOrObj({...context, templates: nestedTemplates});
	}

	// If the template is specified as an object, then it should have a render function
	let render = templateFuncOrObj.render;
	if (!_.isFunction(render)) {
		console.error(
			`ERROR: render function in template ${templateName} must be a function.`
		);
		render = () => undefined;
	}

	const properties = {
		...context.properties,
		..._.get(templates, [templateName, 'properties'], {}),
	};

	return render({...context, templates: nestedTemplates, properties});
}

// Recursively apply template defaults
//
// The defaults from the current level are spread into the children.
// At a given level, if a template is defined, we revert to the default
// in its child templates, unless the user provided an alternative.
export function applyTemplateDefaults(templates, defaults) {
	const updatedTemplates = {};
	// This is used to prevent infinite loops
	// TODO: Perhaps we can do this another way...I don't like adding a key to the object like this.
	if (templates.defaultsApplied) {
		return templates;
	}

	for (let templateName in templates) {
		if (Object.prototype.hasOwnProperty.call(templates, templateName)) {
			const templateFuncOrObj = templates[templateName];
			if (_.isFunction(templateFuncOrObj)) {
				updatedTemplates[templateName] = {
					render: templateFuncOrObj,
					templates: defaults, // TODO: Is this right?
				};
			} else {
				// We (hopefully) have an obect
				// TODO: We can add code to check that it's an object here later
				const obj = templateFuncOrObj;

				// We leave .render and .properties unchanged
				if (!obj.templates) {
					obj.templates = defaults;
				} else if (_.isObject(obj.templates)) {
					// Inherit settings at this level in the child so that we don't need to specify
					// them more than once.
					const inheritedDefaults = {
						...defaults,
						...templates,
						// Force this template back to the default if '...templates' contains it
						[templateName]: defaults[templateName],
					};

					// Recursively apply defaults -- do this BEFORE spreading in defaults
					obj.templates = applyTemplateDefaults(
						obj.templates,
						inheritedDefaults
					);
				}
				updatedTemplates[templateName] = obj;
			}
		}
	}

	return {...defaults, ...updatedTemplates, defaultsApplied: true};
}
