import {renderTemplate} from '../utils';

// Template render functions
export function bodyCell(context) {
	const {properties = {}, templates, column, rowIndex} = context;
	const {focused} = properties;

	const childProperties = {};

	if (column.field == focused.field && rowIndex === focused.row) {
		childProperties.style = {style: {background: 'green', color: 'white'}};
	}

	return renderTemplate('bodyCell', templates, {
		...context,
		properties: childProperties,
	});
}
