import _ from 'lodash';
import {createSelector} from 'reselect';
import u from 'updeep';

const PLUGIN_NAME = 'fixedColumns';

// Exported Selectors
const statePath = ['behaviors', PLUGIN_NAME];
const getFixedColumns = (state) => _.get(state, statePath);

// Split things up into three separate column definitions.
// We can pass the same data into each of the tables with no performance penality.
export function instantiatePlugin(options) {
	const {
		selectors: {getFixedLeft, getFixedRight, getColumns, getEnabled},
	} = options;

	const getFrozenColumnsInternal = createSelector(
		getFixedLeft,
		getFixedRight,
		getColumns,
		(fixedLeft, fixedRight, columns) => {
			// Leave the middle columns in the same order
			const middleColumns = columns.filter(
				(column) =>
					fixedLeft.indexOf(column.field) === -1 &&
					fixedRight.indexOf(column.field) === -1
			);

			// Arrange the frozen columns in the specified order, which may differ from what was in `columns`
			const leftColumns = fixedLeft.map((fieldName) =>
				_.find(columns, (column) => column.field === fieldName)
			);
			const rightColumns = fixedRight.map((fieldName) =>
				_.find(columns, (column) => column.field === fieldName)
			);

			return {leftColumns, middleColumns, rightColumns};
		}
	);

	return {
		selectors: {
			getFixedColumns,
		},

		transformState(state) {
			if (!getEnabled(state)) {
				return state;
			}

			const fixedColumns = getFrozenColumnsInternal(state);
			return u({behaviors: {[PLUGIN_NAME]: fixedColumns}}, state);
		},

		behavior: {
			name: PLUGIN_NAME,
			initialState: {
				leftColumns: [],
				middleColumns: [],
				rightColumns: [],
			},
		},
	};
}
