// Template render functions
export function table(context) {
	const {properties, templates} = context;
	const {leftColumns, middleColumns, rightColumns} = properties;

	// NOTE: Should templates be registered dynamically?  Implicit?  Maybe the partial/plugin provides a new level of customization and needs to allow for new templates/templates.

	const tables = [];

	if (leftColumns.length > 0) {
		tables.push(
			<now-grid {...properties} columns={leftColumns} templates={templates} />
		);
	}

	if (middleColumns.length > 0) {
		tables.push(
			<now-grid
				{...properties}
				columns={middleColumns}
				templates={templates}
				tableLayout="fixed"
			/>
		);
	}

	if (rightColumns.length > 0) {
		tables.push(
			<now-grid {...properties} columns={rightColumns} templates={templates} />
		);
	}
	return <div style={{display: 'flex'}}>{tables}</div>;
}
