// Provides a plugin that handles which handles keyboard events to navigate
// which cell has focus (which can then be used to trigger inline editing
// with another plugin).

import * as KeyCode from 'keycode-js';

import _ from 'lodash';
// import {actions as editingActions} from './gridEditing';
import {makePluginActions} from '../utils';

const PLUGIN_NAME = 'keybooardNavigation';

// Exported Selectors
const statePath = ['behaviors', PLUGIN_NAME];
const focusedPath = [...statePath, 'focused'];
const getFocusedDefault = (state) => _.get(state, focusedPath, {});
export const selectors = {
	getFocused: getFocusedDefault,
};

// Exported Actions
export const actions = makePluginActions(PLUGIN_NAME, [
	'SET_GRID_NAV_POS',
	'CLEAR_GRID_NAV_POS',
]);

// Main export for instantiating the plugin
export function instantiatePlugin(options) {
	const {
		selectors: {
			getEnabled,
			getRowData,
			getColumnOrder,
			getIsEditingCell,
			getFocused = selectors.getFocused,
		},
	} = options;

	function hasDataAndColumns(data, columns) {
		return columns.length >= 1 && data.length >= 1;
	}

	function updateNav(updateState, field, row) {
		updateState({
			path: focusedPath,
			value: {field, row},
			operation: 'set',
		});
	}

	function moveFocusLeft(data, columnOrder, focused, updateState) {
		if (hasDataAndColumns(data, columnOrder)) {
			const currIndex = columnOrder.indexOf(focused.field);
			const newIndex = Math.max(currIndex - 1, 0);

			updateNav(updateState, columnOrder[newIndex], focused.row || 0);
		}
	}

	function moveFocusRight(data, columnOrder, focused, updateState) {
		if (hasDataAndColumns(data, columnOrder)) {
			const currIndex = columnOrder.indexOf(focused.field);
			const newIndex = Math.min(currIndex + 1, columnOrder.length - 1);

			updateNav(updateState, columnOrder[newIndex], focused.row || 0);
		}
	}

	function moveFocusUp(data, columnOrder, focused, updateState) {
		if (hasDataAndColumns(data, columnOrder)) {
			const row = Math.max(
				(focused.row === undefined ? -1 : focused.row) - 1,
				0
			);
			const currIndex = columnOrder.indexOf(focused.field);
			const newIndex = currIndex > -1 ? currIndex : 0; // Ensure we set a valid field when only the row changes

			updateNav(updateState, columnOrder[newIndex], row);
		}
	}

	function moveFocusDown(data, columnOrder, focused, updateState) {
		if (hasDataAndColumns(data, columnOrder)) {
			const row = Math.min(
				(focused.row === undefined ? -1 : focused.row) + 1,
				data.length - 1
			);
			const currIndex = columnOrder.indexOf(focused.field);
			const newIndex = currIndex > -1 ? currIndex : 0; // Ensure we set a valid field when only the row changes

			updateNav(updateState, columnOrder[newIndex], row);
		}
	}

	function clearFocus(updateState) {
		updateNav(updateState, undefined, undefined);
	}

	return {
		behavior: {
			name: PLUGIN_NAME,
			initialState: {
				focused: {field: undefined, row: undefined},
			},
			actionHandlers: {
				// Set the cell with the focus
				// TODO: Connect an onclick handler to this
				[actions.SET_GRID_NAV_POS]: ({action, updateState}) => {
					const {field, row} = action.payload;
					updateNav(updateState, field, row);
				},

				[actions.CLEAR_GRID_NAV_POS]: ({updateState}) => {
					clearFocus(updateState);
				},
			},
			eventHandlers: [
				{
					events: ['keydown'],
					effect({state, updateState, action, dispatch}) {
						if (!getEnabled(state)) {
							return;
						}

						const {
							payload: {event, host},
						} = action;

						// if (host.localName !== 'now-grid') {
						// 	return;
						// }
						action.preventDefault();

						const rowData = getRowData(state);
						const columnOrder = getColumnOrder(state);
						const focused = getFocused(state);

						const isAlreadyEditing = getIsEditingCell(
							state,
							{field: focused.field}, // TODO: Need the full column def here, but I think only the .field is being used right now
							focused.row
						);

						switch (event.keyCode) {
							case KeyCode.KEY_LEFT:
								if (!isAlreadyEditing) {
									moveFocusLeft(rowData, columnOrder, focused, updateState);
								}
								break;

							case KeyCode.KEY_RIGHT:
								if (!isAlreadyEditing) {
									moveFocusRight(rowData, columnOrder, focused, updateState);
								}
								break;

							case KeyCode.KEY_UP:
								if (!isAlreadyEditing) {
									moveFocusUp(rowData, columnOrder, focused, updateState);
								}
								break;

							case KeyCode.KEY_DOWN:
								if (!isAlreadyEditing) {
									moveFocusDown(rowData, columnOrder, focused, updateState);
								}
								break;

							case KeyCode.KEY_ESCAPE:
								dispatch(actions.END_BATCH_EDIT, {commit: false});
								clearFocus(updateState);
								break;

							case KeyCode.KEY_RETURN:
								const {field, row} = focused;

								// TODO: Add this back in
								// if (!isAlreadyEditing) {
								// 	// Start editing a single cell
								// 	dispatch(editingActions.START_BATCH_EDIT, {
								// 		cellsToEdit: [{field, row}],
								// 	});
								// }
								break;
						}
						// TODO: Need to scroll selected row to be visible
					},
					target: document,
					capture: true,
				},
			],
		},
		selectors: {
			...options,
			getFocused,
		},
	};
}
