// Template render functions

import {actions} from '../plugins/filterByColumnsPlugin';
import {renderTemplate} from '../utils';
const {SET_FILTER_FOR_FIELD} = actions;

export function headRow(context) {
	const {templates, columns, dispatch, properties} = context;

	const head = [renderTemplate('headRow', templates, context)];

	if (properties.isEnabled) {
		head.push(
			// A second head row will contain the inputs for filtering
			<tr>
				{columns.map((column, columnIndex) => (
					<th>
						<input
							type="text"
							value=""
							placeholder="Search..."
							style={{width: '100%'}}
							oninput={(event) => {
								const filter = _.get(event, ['target', 'value'], '').trim();
								dispatch(SET_FILTER_FOR_FIELD, {
									field: column.field,
									filter,
								});

								return true;
							}}
						/>
					</th>
				))}
			</tr>
		);
	}
	return head;
}
